.controls {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding: 10px;
    background-color: #d8d8d8;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.buttonGroup {
    display: flex;
    gap: 20px;
}

/* Responsive Styles */
@media (max-width: 768px) {
    .controls {
        flex-direction: column;
        gap: 10px;
    }

    .buttonGroup {
        flex-direction: column;
        gap: 10px;
    }
}