/* Global Styles */
body {
  margin: 0;
  font-family: 'Arial', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f4f6f9; /* Light grey background color */
  color: #333; /* Dark grey text color */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  padding: 20px;
  background-color: #f0f2ff;
  border: 1px solid #ddd;
  border-radius: 10px;
  margin: 20px auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 1200px;
}

.controls {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.ruleContainer {
  overflow: hidden; /* Clear floats */
}

/* Keyframes for Logo Spin Animation */
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Styles */
@media (max-width: 1200px) {
  .container {
    max-width: 100%;
    padding: 15px;
  }

  .controls {
    flex-direction: column;
    gap: 10px;
  }

  .productTable th, .productTable td {
    padding: 8px;
  }

  .form {
    padding: 10px;
  }

  .formRow {
    flex-direction: column;
  }

  .buttonGroup {
    flex-direction: column;
    gap: 5px;
  }
}

@media (max-width: 768px) {
  .container {
    padding: 10px;
  }

  .App-header {
    font-size: calc(8px + 2vmin);
  }

  .controls {
    flex-direction: column;
    gap: 10px;
  }

  .productTableContainer {
    overflow-x: auto; /* Add horizontal scroll for product table */
  }

  .productTable {
    min-width: 600px; /* Ensure table doesn't shrink too much */
  }

  .productTable th, .productTable td {
    padding: 8px;
  }

  .form {
    padding: 10px;
  }

  .formRow {
    flex-direction: column;
  }

  .buttonGroup {
    flex-direction: column;
    gap: 5px;
  }
}